* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

html,
body {
  height: 100%;
}
.FavoritesHeart {
  color: white;
  font-size: 40px;
  cursor: pointer;
}

.FavoritesHeart:hover {
  color: pink;
}

.text_logo {
  font-size: 28px;
  color: #dec79b;
}

/* Loader */
.loader {
  border: 8px solid #c0c0c0;
  border-top: 8px solid rgb(24, 24, 30);
  border-radius: 50%;
  width: 65px;
  height: 65px;
  -webkit-animation: spin 1.5s linear infinite;
          animation: spin 1.5s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* Movies Grid */

.moviesGrid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 45px;
  margin: 45px auto 75px auto;
  width: 82%;
}

.moviesGridItem {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  height: 400px;
  width: 250px;
  box-shadow: 4px 5px 20px rgb(179, 179, 179);
  position: relative;
  margin: 0 auto;
}

.moviesGridImg {
  width: 100%;
  cursor: pointer;
  z-index: 0;
}

.moviesGridBottom {
  height: 100px;
  width: 250px;
  margin: 0 auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: rgb(245, 245, 245);
}

#movieHeart {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

/* Home Page */
.home {
  height: 92vh;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  background-color: rgb(222, 230, 236);
}

.home_container {
  height: 92vh;
  display: -webkit-flex;
  display: flex;
}

.movie_section {
  -webkit-flex: 1 1;
          flex: 1 1;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  transition: -webkit-flex 0.5s;
  transition: flex 0.5s;
  transition: flex 0.5s, -webkit-flex 0.5s;
}

.movie_section:hover {
  -webkit-flex: 2 1;
          flex: 2 1;
}

.movie_section:hover .home_content {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.7);
}

.movie_section:hover .bottom_line_text {
  opacity: 0;
}

.home_content {
  z-index: 2;
  text-align: center;
  color: white;
  opacity: 0;
  transition: all 0.4s ease;
  /* background-color: rgba(0, 0, 0, 0); */
  height: 100%;
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.overlay {
  background-color: rgba(0, 0, 0, 0.6);
  width: 100%;
  height: 100%;
  position: absolute;
  transition: background-color 0.5s ease;
}

.overlay_dark {
  background-color: rgba(0, 0, 0, 0.7);
}

.right_line {
  height: 97%;
  border-left: 2px solid #dec79b34;
  width: 2%;
  z-index: 2;
  position: absolute;
  right: 3%;
  top: 0;
}

.bottom_line {
  width: 95%;
  z-index: 2;
  border-top: 2px solid #dec79b34;
  position: absolute;
  left: 0;
  bottom: 3%;
}

.bottom_line_text {
  position: absolute;
  left: 0;
  bottom: 3%;
  margin-bottom: 20px;
  margin-left: 15px;
  color: rgb(184, 184, 184);
  opacity: 1;
  transition: opacity 0.4s ease;
}

/* Header Search Bar */
.header-form {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: absolute;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.header-search {
  height: 35px;
  width: 0;
  outline: none;
  padding-left: 15px;
  border: none;
  border-radius: 15px;
  transition: all 0.6s;
}

.header-icon {
  height: 45px;
  width: 45px;
  background-color: #dec79b;
  border-radius: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-transform: translateX(-20px);
          transform: translateX(-20px);
  font-size: 1.1rem;
  cursor: pointer;
  color: #18181e;
}

.header-search__active {
  width: 250px;
}

/* Single Movie Page */

.movie_backdrop__container {
  min-height: 92vh;
  min-width: 100%;
  overflow: hidden;
  position: relative;
}

.movie_backdrop {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 92vh;
  width: 100%;
}

.movie_backdrop__tint {
  height: 100%;
  width: 100%;
  background: linear-gradient(0deg, rgb(0, 0, 0) 5%, rgba(0, 0, 0, 0.45) 92%)
    center center no-repeat;
}

.tagline {
  position: absolute;
  right: 0;
  width: 30%;
  top: 50px;
  font-size: 2.1rem;
  color: rgba(225, 225, 225, 0.3);
  margin: 0 10%;
  font-style: cursive;
  font-family: Overlock;
  text-align: right;
}

/* Movie Trailer */

.hidePlay {
  display: none;
}

.showPlay {
  display: -webkit-flex;
  display: flex;
}

/* Footer */

.footerContainer {
  width: 80%;
  height: 100%;
  margin: 0 auto;
  padding: 70px 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
}

.footerInfo {
  -webkit-flex-direction: row;
          flex-direction: row;
}
.footerCopyright {
  margin-top: 15px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: absolute;
  bottom: 40px;
  width: 100%;
}

@media (max-width: 700px) {
  .footerInfo {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: 0 auto;
    text-align: center;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }
  .footerContainer {
    padding: 25px 0;
  }
  .footerCopyright {
    bottom: 25px;
  }
  .footerTMDB {
    margin-top: 15px;
  }
  .footerContactMe {
    margin-top: 10px;
  }

  /* Single Movie Page */
  .tagline {
    width: 60%;
  }

  /* Home Page */
  .home_container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }

  /* Nav Bar */
  .text_logo {
    display: none;
  }

  .FavoritesHeart {
    display: none;
  }
}

.container {
  width: 80%;
  height: auto;
  margin: 60px auto;
  position: relative;
}

.fa-chevron-left {
  position: absolute;
  top: calc(50% - 0.5em);
  left: -40px;
  font-size: 40px;
  line-height: 30px;
  padding: 5px 0;
  cursor: pointer;
  z-index: 10;
  color: #18181e;
}

.fa-chevron-right {
  position: absolute;
  top: calc(50% - 0.5em);
  right: -40px;
  font-size: 40px;
  line-height: 30px;
  padding: 5px 0;
  cursor: pointer;
  z-index: 10;
  color: #18181e;
}

.items {
  overflow-x: scroll;
  white-space: nowrap;
  padding: 25px 0;
  position: relative;
}
.items::-webkit-scrollbar {
  display: none;
}

.item {
  width: 154px;
  height: 190px;
  background-color: gray;
  margin-right: 70px;
  display: inline-block;
}

.itemImgContainer {
  height: 220px;
  width: 154px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 8px 8px 8px rgb(214, 214, 214);
}

.itemImg {
  height: 100%;
  width: 100%;
  background-color: white;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.itemInfo {
  background-color: rgb(253, 241, 241);
  width: 154px;
}

.active {
  cursor: grabbing;
  cursor: -webkit-grabbing;
}

.boob {
  height: 400px;
}

.hr_style {
  width: 60%;
  margin: 60px auto 0 auto;
  border: 0;
  height: 1px;
  background: lightgray;
  background-image: linear-gradient(to right, #ccc, lightgray, #ccc);
}

